import React from 'react';
import { Link } from 'react-router-dom';
import './components/Home.scss';
import {Card} from 'react-bootstrap';
import Screenshot_1 from "./assets/Screenshot_1.png";
import Screenshot_2 from "./assets/Screenshot_2.png";
import Footer from "./components/Footer";

const Home = () =>{
    return (
        <div>
            <div>
                <h2 className="center">Pages in our sites</h2>
            </div>
            <Card bg="dark" text="white" style={{ width: '25rem', }}>
                <Card.Img variant="top" src={Screenshot_1} />
                <Card.Body>
                    <Card.Title>Bitcoin course page</Card.Title>
                    <Card.Text>
                        On this page you can see live Bitcoin course.
                    </Card.Text>
                    <Link className="btn btn-primary" to="/btcourse">Go to page</Link>
                </Card.Body>
            </Card>
            {/* padding div  */}
            <div className="padding-top4ik"></div>
            {/* padding div */}
            <Card bg="dark" text="white" style={{ width: '25rem' }}>
                <Card.Img variant="top" src={Screenshot_2} />
                <Card.Body>
                    <Card.Title>Privat bank currency course</Card.Title>
                    <Card.Text>
                        On this page you can see Privat bank currency courses.
                    </Card.Text>
                    <Link className="btn btn-primary" to="/btcourse">Go to page</Link>
                </Card.Body>
            </Card>
            {/* padding div  */}
            <div className="padding-top4ik"></div>
            {/* padding div */}
        </div>
    )
}
export default Home;