import React from 'react'

const PageNotFound = () =>{
    return(
        <div className="jumbotron">
            <div className="container">
                <h1 className="display-4">Page not found</h1>
                <p className="lead">This is not the web page you looking for.</p>
            </div>
        </div>
    )
}
export default PageNotFound;